
body {
  font-size: 15px;
  font-family: "Microsoft YaHei",Arial,Helvetica,sans-serif;
  color: #222;
  margin: 0;
  padding: 0;
}
.meat {
  font-size: 9em;
  text-align: center;
  font-family: "Microsoft JhengHei", sans-serif;
}
.meat ul {
  padding: 0;
}
.meat ul>li {
  display: inline-block;
}
.meat ul>li:hover {
  cursor: pointer;
  color: white;
}
.signature {
  position: absolute;
  bottom: 0;
}



