
.main {
  margin-top: 20px;
  font-size: 1.2em;
}
.main>label {
  color: cadetblue;
  font-weight: 700;
  font-size: 2em;
  cursor: pointer;
}
.main-row {
  border-bottom: 1px solid #f2f2f2;
}
.main-row>div {
  display: inline-block;
  width: 80%;
  margin-top: 20px;
  color: #333;
}
.main-row>span {
  font-size: 0.6em;
  color: #aaa;
}
.bottom-panel label {
  float: left;
  width: 60px;
  display: inline-block;
}
.bottom-panel input {
  width: 90%;
  height: 25px;
  font-size: 1em;
}
.bottom-panel textarea {
  float: left;
  width: 90%;
  height: 300px;
  font-size: 1em;
  margin-bottom: 20px;
}
.bottom-panel .bottom-row {
  margin: 20px 0;
  line-height: 25px;
  font-size: 1.2em;
}
.bottom-panel button {
  margin-left: 60px;
}



